import React from "react";

import { Link, graphql, useStaticQuery } from "gatsby";

import { IPostPreviewQueryResult } from "../types/post.type";

import ContentContainer from "./contentContainer";
import PostPreview from "./postPreview";

const PostSection = () => {
  const postData = useStaticQuery(query);

  const post = (
    postData?.allMarkdownRemark?.nodes?.length
      ? postData.allMarkdownRemark.nodes[0]
      : null
  ) as IPostPreviewQueryResult | null;

  return (
    <>
      {post ? (
        <section className="w-full bg-main-100 text-secondary-100 py-20">
          <ContentContainer className="flex-col items-center">
            <div className="grid grid-cols-3 w-full items-center pb-10 md:pb-16">
              <h3 className="col-start-1 col-span-2 lg:col-start-2 lg:col-span-1 text-5xl font-bold">
                New in blog
              </h3>
              <Link
                to="/blog"
                className="text-end text-2xl text-secondary-10 hover:text-secondary-100 cursor-pointer"
              >
                ALL POSTS &#8594;
              </Link>
            </div>

            <PostPreview post={post} fontColor="secondary" />
          </ContentContainer>
        </section>
      ) : null}
    </>
  );
};

export default PostSection;

export const query = graphql`
  query {
    allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "posts" } } }
      sort: { frontmatter: { date: DESC } }
      limit: 1
    ) {
      nodes {
        id
        frontmatter {
          date(formatString: "MMM DD, YYYY")
          author
          title
          text
          cover {
            childImageSharp {
              gatsbyImageData(
                formats: [AUTO, WEBP, AVIF]
                placeholder: BLURRED
                width: 600
              )
            }
          }
        }
      }
    }
  }
`;
