import React from "react";

import { useStaticQuery, graphql } from "gatsby";
import { GatsbyImage, ImageDataLike, getImage } from "gatsby-plugin-image";

import useEmblaCarousel from "embla-carousel-react";
import Autoplay from "embla-carousel-autoplay";

import { ILogosQueryResult } from "../types/client.type";

const ClientsSection = () => {
  const [emblaRef, emblaApi] = useEmblaCarousel(
    { loop: true, dragFree: true, duration: 10000, align: 0 },
    [
      Autoplay({
        delay: 0,
        stopOnInteraction: false,
        stopOnMouseEnter: true,
      }),
    ]
  );

  const logosData = useStaticQuery(query);

  const logosArr = (
    logosData?.allFile?.nodes?.length
      ? logosData.allFile.nodes.map((node: ImageDataLike & { id: string }) => ({
          id: node.id,
          img: getImage(node),
        }))
      : null
  ) as ILogosQueryResult[] | null;

  return (
    <>
      {logosArr?.length ? (
        <section className={`h-36`} id="clients">
          <div className="h-full overflow-hidden" ref={emblaRef}>
            <div
              className="h-full flex items-center"
              onMouseEnter={() => {
                emblaApi?.internalEngine().animation.stop();
              }}
              onMouseLeave={() => {
                emblaApi?.internalEngine().animation.start();
              }}
            >
              {logosArr.map(({ id, img }) => (
                <div
                  key={id}
                  className="h-full shrink-0 min-w-0 basis-1/3 sm:basis-1/4 md:basis-2/6 lg:basis-1/6 p-12 cursor-pointer flex items-center content-center grayscale"
                >
                  {<GatsbyImage image={img} alt="logo" />}
                </div>
              ))}
            </div>
          </div>
        </section>
      ) : null}
    </>
  );
};

export default ClientsSection;

export const query = graphql`
  query {
    allFile(filter: { sourceInstanceName: { eq: "clients" } }) {
      nodes {
        childImageSharp {
          gatsbyImageData(formats: [AUTO, AVIF, WEBP])
        }
        id
      }
    }
  }
`;
