import React from "react";

import { graphql, useStaticQuery } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

import ContentContainer from "./contentContainer";

export const BackgroundImage = () => {
  const bgImageData = useStaticQuery(query);

  const bgImage = getImage(bgImageData.file.childImageSharp);

  return (
    <>
      {bgImage ? (
        <GatsbyImage
          image={bgImage}
          alt="background"
          className="absolute left-1/3 h-full w-full"
        />
      ) : null}
    </>
  );
};

const Banner = ({
  heightClass,
  title,
  text,
}: {
  heightClass: string;
  title: string;
  text: string;
}) => {
  return (
    <div
      className={`w-full bg-secondary-70 relative overflow-hidden ${heightClass}`}
    >
      <ContentContainer
        className={`h-full flex-col items-left justify-center overflow-hidden`}
      >
        <BackgroundImage />
        <h2 className="text-bold text-7xl text-main-100 mb-8">{title}</h2>
        <p className="max-w-xl w-full text-lg">{text}</p>
      </ContentContainer>
    </div>
  );
};

export default Banner;

export const query = graphql`
  query {
    file(sourceInstanceName: { eq: "common" }, name: { eq: "banner" }) {
      childImageSharp {
        gatsbyImageData(formats: [AUTO, AVIF, WEBP], placeholder: BLURRED)
      }
    }
  }
`;
