import React from "react";

import { GatsbyImage, getImage } from "gatsby-plugin-image";

import { IReview } from "../types/review.type";

const Review = ({
  data: { company, person, position, location, text, image },
}: {
  data: IReview;
}) => {
  const avatar = getImage(image);

  return (
    <div className="flex flex-row flex-nowrap p-6">
      <div className="basis-1/3 p-2 flex flex-col items-center">
        <div
          className={`rounded-full h-28 w-28 mx-auto mb-8 overflow-hidden flex justify-center items-center`}
        >
          {avatar ? (
            <GatsbyImage
              image={avatar}
              alt={company}
              className="h-full object-cover"
            />
          ) : null}
        </div>
        <h5 className="text-lg text-main-70"> {person} </h5>
        <h5 className="text-lg text-main-70">
          {position}, {company}
        </h5>
        <h5 className="text-lg text-main-70"> {location} </h5>
      </div>
      <div className="shrink-0 basis-2/3 text-base text-main-70 p-2">
        {text}
      </div>
    </div>
  );
};

export default Review;
