import React from "react";

import { Link } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

import { IPostPreviewQueryResult } from "../types/post.type";

const PostPreview = ({
  post,
  className,
  fontColor,
}: {
  post: IPostPreviewQueryResult;
  className?: string;
  fontColor: "main" | "secondary";
}) => {
  const cover = getImage(post.frontmatter.cover);
  const { date, title, text } = post.frontmatter;

  const fontColorClass100 =
    fontColor === "main" ? "text-main-100" : "text-secondary-100";
  const fontColorClass10 =
    fontColor === "main" ? "text-main-10" : "text-secondary-10";
  const fontColorHover =
    fontColor === "main" ? "hover:text-main-100" : "hover:text-secondary-100";

  return (
    <Link
      to={`/blog/post/${post.id}`}
      className={`flex w-full ${fontColorClass10} ${fontColorHover} ${className}`}
    >
      <div className="hidden md:flex md:basis-1/3 shrink-0">
        {cover ? <GatsbyImage image={cover} alt={title} /> : null}
      </div>
      <div className="p-8 pl-12 flex flex-col basis-1/2 md:basis-1/3 shrink-0">
        <p className={`text-xl ${fontColorClass10} pb-8`}>{date}</p>
        <h3 className={`text-4xl ${fontColorClass100}`}> {title} </h3>
      </div>
      <div className="flex flex-col justify-center basis-1/2 md:basis-1/3 shrink-0">
        <p className={`text-xl ${fontColorClass10} pb-3`}>
          {text.split(" ").slice(0, 40).join(" ")}...
        </p>
        <p className={`text-xl`}>READ MORE &#8594;</p>
      </div>
    </Link>
  );
};

export default PostPreview;
